
<template>
  <div class="bg-gray-100">
    <div id="wrapper" class="flex flex-col justify-between h-screen">
      <!-- header-->
      <div class="bg-white py-4 shadow dark:bg-gray-800">
        <div class="max-w-6xl mx-auto">
          <div class="flex items-center lg:justify-between justify-around">
            <a href="trending.html">
              <img src="../assets/images/logo.png" alt="" class="w-32" />
            </a>

            <div
              class="capitalize flex font-semibold hidden lg:block my-2 space-x-3 text-center text-sm"
            >
              <a href="javascript:void(0)" @click="toLogin()" class="py-3 px-4"
                >Login</a
              >
              <a
                href="javascript:void(0)"
                class="bg-pink-500 pink-500 px-6 py-3 rounded-md shadow text-white"
                @click="toRegister()"
                >Register</a
              >
            </div>
          </div>
        </div>
      </div>

      <!-- Content-->
      <div>
        <div
          class="lg:p-12 max-w-md max-w-xl lg:my-0 my-12 mx-auto p-6 space-y-"
        >
          <h1 class="lg:text-3xl text-xl font-semibold mb-6">Log in</h1>
          <p class="mb-2 text-black text-lg">Email or Username</p>
          <form action="">
            <input
              type="text"
              placeholder="example@mydomain.com"
              class="bg-gray-200 mb-2 shadow-none dark:bg-gray-800"
              style="border: 1px solid #d3d5d8 !important"
            />
            <input
              type="text"
              placeholder="***********"
              class="bg-gray-200 mb-2 shadow-none dark:bg-gray-800"
              style="border: 1px solid #d3d5d8 !important"
            />
            <div class="flex justify-between my-4">
              <div class="checkbox">
                <input type="checkbox" id="chekcbox1" checked />
                <label for="chekcbox1"
                  ><span class="checkbox-icon"></span>Remember Me</label
                >
              </div>
              <a href="#"> Forgot Your Password? </a>
            </div>
            <button
              type="submit"
              class="bg-gradient-to-br from-pink-500 py-3 rounded-md text-white text-xl to-red-400 w-full"
              @click="login()"
            >
              Login
            </button>
            <div class="text-center mt-5 space-x-2">
              <p class="text-base">
                Not registered?
                <a href="form-register.html" class=""> Create a account </a>
              </p>
            </div>
          </form>
        </div>
      </div>

      <!-- Footer -->

      <div class="lg:mb-5 py-3 uk-link-reset">
        <div
          class="flex flex-col items-center justify-between lg:flex-row max-w-6xl mx-auto lg:space-y-0 space-y-3"
        >
          <div class="flex space-x-2 text-gray-700 uppercase">
            <a href="#"> About</a>
            <a href="#"> Help</a>
            <a href="#"> Terms</a>
            <a href="#"> Privacy</a>
          </div>
          <p class="capitalize">© copyright 2020 by Instello</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "login",
  data() {
    return {
      username: "wdl1510778717",
      password: "dnmb3930153652",
    };
  },
  methods: {
    login() {
      this.$router.push({ name: "profile" });
    },
    toLogin() {
      this.$router.push({ name: "login" });
    },
    toRegister() {
      this.$router.push({ name: "register" });
    },
  },
  mounted() {},
};
</script>

<style lang="scss" scoped>
</style>



